
.bigImage {
    width: 100%;
    height: 470px;
  }
  
  p {
    margin: 0;
  }
  
  .blockStyle {
    width: 290px;
    height: 90px;
    border: 1px solid black;
    border-radius: 10px;
    padding: 0 10px;
    box-sizing: border-box;
    margin: 20px auto;
    background-color: rgb(247, 243, 243);
  
  }
  
  .imgBlock img {
    display: block;
    max-width: 100%;
    margin: 5px auto;
    border-radius: 10px;
  
  }
  .applogo {
    height: 60px;
    width: 60px;
    border-radius: 50%;
  }
  
  h4 {
    margin-top: 10px;
    float: left;
    padding-right: 10px;
    letter-spacing: 2px;
  }
  
  .Appheader {
    display: flex;
    margin: 0 auto;
    width: 700px;
    height: 450px;
    border: 1px solid white;
    background-color: black;
    color: white;
    padding: 20px;
  
  }
  
  .Appblock {
    margin-top: 10px;
    margin-left: 20px;
  }
  
  span {
    display: inline-block;
    color: grey;
    padding: 10px;
    margin-top: 2px;
  }
  
  .ImageWomen {
    width: 600px;
    height: 300px;
    border-radius: 30px;
    margin-top: 20px;
  }
  .blokBot{
    display: flex;
    justify-content: space-around;
  }